
:root{
  --hauteur-menu: 60px;
}

nav {
  font-family: Roboto, sans-serif;
  font-size: 18px;
  position: sticky;
  top: 0;
  > ul {
    display: flex;
    text-align: center;
    box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.3);
    height: var(--hauteur-menu);
    > li {
      min-width: 150px;
      background-color: black;
      position: relative;
      height: 100%;
      flex: 1;
      &:hover > a{
        color: #FF0101;
      }
      > span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-family: Roboto;
          text-decoration: none;
          font-style: normal;
          font-weight: 500;
          font-size: 24px;
          line-height: 142.6%;
          display: flex;
          text-align: right;
          color: #FFFFFF;
          margin-right: 90px;
      }
    }
  }
  ul{
    list-style-type: none;
  }
}

.dropdown__link:hover{
  text-decoration: none;
}

li a {
  text-decoration: none;
  color: white;
}


.sous-menu {
  width: 100px;
  margin-top: var(--hauteur-menu);
  width: 100%;
  text-align: left;
  background-color: black;
  border-radius: 2px;
  overflow: hidden;
  max-height: 0;
  > li > a {
    height: 50px;
    padding-left: 20px;
    width: 100%;
    align-items: center;
    display: flex;
  }
}


@keyframes apparitionSousMenu {
  0% {
    box-shadow: 0px 3px 3px 1px rgba(0,0,0,0);
    border-top: 3px solid #FF0101;
  }
  30% {
    box-shadow: 0px 3px 3px 1px rgba(0,0,0,0.3);
  }
  100% {
    max-height: 50em;
    border-top: 3px solid #FF0101;
    box-shadow: 0px 3px 3px 1px rgba(0,0,0,0.3);
  }
}

.menu-deroulant:hover > .sous-menu {
  animation: apparitionSousMenu 1s forwards;
}

.sous-menu > li:hover {
  background-color: rgba(33, 105, 236, 0.3);
}

.sous-menu > li:hover > a {
  color: white;
}